import {Component, OnInit, Input, Output} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {EntityService} from '../entity.service';
import {School, Degree, Department, ProfTitle , Teacher , Response } from '../entity';
@Component({
  selector: 'app-update-teacher',
  templateUrl: './updateTeacher.component.html',
})
export class UpdateTeacherComponent implements OnInit {
  @Input() profTitles: ProfTitle[];
  degrees: Degree[];
  departments: Department[];
  schools: School[];
  teacher: Teacher;
  profTitle: ProfTitle;
  degree: Degree;
  department: Department;
  school: School;
  constructor(
    private route: ActivatedRoute,
    private teacherService: EntityService,
    private location: Location
  ) {}

  ngOnInit() {
    this.getProfTitles(); // 下拉菜单赋值
    this.getDegrees();
    this.getDepartments();
    const id = this.route.snapshot.params.id; // 要修改教师的id
    this.teacherService.getTeacher(id)
      .subscribe(res => {
        this.teacher = res;
      });
  }

  compareFn1(o1: ProfTitle, o2: ProfTitle): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }
  compareFn2(o1: Degree, o2: Degree): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }
  compareFn3(o1: Department, o2: Department): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }

  getProfTitles(): void {
    this.teacherService.getProfTitles()
      .subscribe(res => this.profTitles = res);
  }
  getDegrees(): void {
    this.teacherService.getDegrees()
      .subscribe(res => this.degrees = res);
  }
  getDepartments(): void {
    this.teacherService.getDepartments()
      .subscribe(res => this.departments = res);
  }
  getDepSch(id: number): void {
    this.teacherService.getDepSch (id).subscribe(res => this.departments = res);
  }

  save(): void { // 多个文本框数据
    console.log(this.teacher);
    this.teacherService.updateTeacher(this.teacher)
      .subscribe((res) => {
        alert(res.message);
        this.goBack();
      });
  }

  goBack(): void {
    this.location.back();
  }
}
