import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { School } from '../entity';
import { EntityService } from '../entity.service';

@Component({
  selector: 'app-detaill',
  templateUrl: './detaillSchool.component.html',
  styleUrls: ['./detaillSchool.component.css']
})
export class DetaillSchoolComponent implements OnInit {

  @Input() school: School;

  constructor(
    private route: ActivatedRoute,
    private schoolService: EntityService,
    private location: Location
  ) {}

  ngOnInit(): void {
    this.route.queryParams
           .subscribe((params: School) => {
               this.school = params;
             });
  }

  goBack(): void {
    this.location.back();
  }
}
