import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { EntityService } from '../entity.service';
import {School, Degree, Department, ProfTitle , Teacher , Teacher1, Response } from '../entity';
@Component({
  selector: 'app-add-teacher',
  templateUrl: './addTeacher.component.html',
  styleUrls: ['./addTeacher.component.css']
})
export class AddTeacherComponent implements OnInit {
  @Input() profTitles: ProfTitle[];
  degrees: Degree[];
  departments: Department[];
  schools: School[];
  teacher: Teacher1;
  profTitle: ProfTitle;
  degree: Degree;
  department: Department;
  school: School;
  constructor(
    private route: ActivatedRoute,
    private teacherService: EntityService,
    private location: Location
  ) {}

  ngOnInit() {
    this.getProfTitles();
    this.getDegrees();
    this.getDepartments();
  }
  getProfTitles(): void {
    this.teacherService.getProfTitles()
      .subscribe(res => this.profTitles = res);
  }
  getDegrees(): void {
    this.teacherService.getDegrees()
      .subscribe(res => this.degrees = res);
  }
  getDepartments(): void {
    this.teacherService.getDepartments()
      .subscribe(res => this.departments = res);
  }
  getSchools(): void {
    this.teacherService.getSchools()
      .subscribe(res => this.schools = res);
  }
  getDepSch(id: number): void {
    this.teacherService.getDepSch (id).subscribe(res => this.departments = res);
  }
  save( name: string, profTitle: ProfTitle, degree: Degree, department: Department): void { // 多个文本框数据
    this.teacher = { name: name.trim(), profTitle: this.profTitle, degree: this.degree, department: this.department};
    if (!name) { return; }
    this.teacherService.addTeacher( this.teacher )
      .subscribe(() => this.goBack());
  }
  goBack(): void {
    this.location.back();
  }
}
