import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { EntityService } from '../entity.service';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-add-profTitle',
  templateUrl: './addProfTitle.component.html',
  styleUrls: ['./addProfTitle.component.css']
})
export class AddProfTitleComponent implements OnInit {
  @Input() profTitle: { no: string; description: string; remarks: string };
  constructor(
    private route: ActivatedRoute,
    private profTitleService: EntityService,
    private location: Location
  ) {}

  ngOnInit(): void {}

  save( no: string, description: string, remarks: string): void { //多个文本框数据
    this.profTitle = {no: no.trim(), description: description.trim(), remarks: remarks.trim()};
    if (!description) { return; }
    this.profTitleService.addProfTitle(this.profTitle)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }
}
