import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { Department } from '../entity';
import { EntityService } from '../entity.service';

@Component({
  selector: 'app-detaill',
  templateUrl: './detaillDepartment.component.html',
  styleUrls: ['./detaillDepartment.component.css']
})
export class DetaillDepartmentComponent implements OnInit {

  @Input() department: Department;

  constructor(
    private route: ActivatedRoute,
    private departmentService: EntityService,
    private location: Location
  ) {}

  ngOnInit(): void {
    this.route.queryParams
           .subscribe((params: Department) => {
               this.department = params;
             });
  }

  goBack(): void {
    this.location.back();
  }
}
