import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { Degree } from '../entity';
import { EntityService } from '../entity.service';

@Component({
  selector: 'app-detaill',
  templateUrl: './detaillDegree.component.html',
  styleUrls: ['./detaillDegree.component.css']
})
export class DetaillDegreeComponent implements OnInit {

  @Input() degree: Degree;

  constructor(
    private route: ActivatedRoute,
    private degreeService: EntityService,
    private location: Location
  ) {}

  ngOnInit(): void {
    this.route.queryParams
           .subscribe((params: Degree) => {
               this.degree = params;
             });
  }

  goBack(): void {
    this.location.back();
  }
}
