import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { EntityService } from '../entity.service';

@Component({
  selector: 'app-add-degree',
  templateUrl: './addDegree.component.html',
  styleUrls: ['./addDegree.component.css']
})
export class AddDegreeComponent implements OnInit {
  @Input() degree: { no: string; description: string; remarks: string };
  constructor(
    private route: ActivatedRoute,
    private degreeService: EntityService,
    private location: Location
  ) {}

  ngOnInit(): void {}

  save( no: string, description: string, remarks: string): void { //多个文本框数据
    this.degree = {no: no.trim(), description: description.trim(), remarks: remarks.trim()};
    if (!description) { return; }
    this.degreeService.addDegree(this.degree)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }
}
