import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import {School, Degree, Department, ProfTitle, Teacher, Response, Department1, Teacher1} from './entity';
const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};
@Injectable({
  providedIn: 'root'
})
export class EntityService {
  response: Response;
  private schoolUrl = '../school.ctl';  //sevlet的相对路径，
  private degreeUrl = '../degree.ctl';
  private departmentUrl = '../department.ctl';
  private profTitleUrl = '../profTitle.ctl';
  private teacherUrl = '../teacher.ctl';
  constructor(private http: HttpClient) { }
  /** GET schools from the server */
  getSchools(): Observable<School[]> {
    return this.http.get<School[]>(this.schoolUrl + ' ')
      .pipe(
        catchError(this.handleError<School[]>('getSchools', []))
      );
  }
  /** GET school by id. Will 404 if id not found */
  getSchool(id: number): Observable<School> {
    const url = this.schoolUrl + '?id=' + id;
    return this.http.get<School>(url).pipe(
      catchError(this.handleError<School>(`getSchool id=${id}`))
    );
  }

  /** POST: add a new college to the server */
  addSchool(school: { no: string; description: string; remarks: string }): Observable<Response> {
    return this.http.post<Response>(this.schoolUrl + ' ', school, httpOptions).pipe(
      catchError(this.handleError<any>('addSchool'))
    );
  }


  /** DELETE: delete the college from the server */
  deleteSchool(school: School | number): Observable<Response> {
    const id = typeof school === 'number' ? school : school.id;
    return this.http.delete<Response>(this.schoolUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteSchool'))
    );
  }


  /** PUT: update the hero on the server */
  updateSchool(school: School): Observable<Response> {
    return this.http.put<Response>(this.schoolUrl + ' ', school, httpOptions).pipe(
      catchError(this.handleError<Response>('updateSchool'))
    );
  }
  /** GET degrees from the server */
  getDegrees(): Observable<Degree[]> {
    return this.http.get<Degree[]>(this.degreeUrl + ' ')
      .pipe(
        catchError(this.handleError<Degree[]>('getDegrees', []))
      );
  }
  /** GET degree by id. Will 404 if id not found */
  getDegree(id: number): Observable<Degree> {
    const url = this.degreeUrl + '?id=' + id;
    return this.http.get<Degree>(url).pipe(
      catchError(this.handleError<Degree>(`getDegree id=${id}`))
    );
  }

  /** POST: add a new college to the server */
  addDegree(degree: { no: string; description: string; remarks: string }): Observable<Response> {
    return this.http.post<Response>(this.degreeUrl + ' ', degree, httpOptions).pipe(
      catchError(this.handleError<any>('addDegree'))
    );
  }

  /** DELETE: delete the college from the server */
  deleteDegree(degree: Degree | number): Observable<Response> {
    const id = typeof degree === 'number' ? degree : degree.id;
    return this.http.delete<Response>(this.degreeUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteDegree'))
    );
  }

  /** PUT: update the hero on the server */
  updateDegree(degree: Degree): Observable<Response> {
    return this.http.put<Response>(this.degreeUrl + ' ', degree, httpOptions).pipe(
      catchError(this.handleError<Response>('updateDegree'))
    );
  }
  /** GET departments from the server */
  getDepartments(): Observable<Department[]> {
    return this.http.get<Department[]>(this.departmentUrl + ' ')
      .pipe(
        catchError(this.handleError<Department[]>('getDepartments', []))
      );
  }
  /** GET department by id. Will 404 if id not found */
  getDepartment(id: number): Observable<Department> {
    const url = this.departmentUrl + '?id=' + id;
    return this.http.get<Department>(url).pipe(
      catchError(this.handleError<Department>(`getDepartment id=${id}`))
    );
  }

  /** POST: add a new department to the server */
  // tslint:disable-next-line:max-line-length
  addDepartment(department: Department1): Observable<Response> {
    return this.http.post<Response>(this.departmentUrl + ' ', department, httpOptions).pipe(
      catchError(this.handleError<any>('addDepartment'))
    );
  }
  /** DELETE: delete the department from the server */
  deleteDepartment(department: Department | number): Observable<Response> {
    const id = typeof department === 'number' ? department : department.id;
    return this.http.delete<Response>(this.departmentUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteDepartment'))
    );
  }

  /** PUT: update the hero on the server */
  updateDepartment(department: Department): Observable<Response> {
    return this.http.put<Response>(this.departmentUrl + ' ', department, httpOptions).pipe(
      catchError(this.handleError<Response>('updateDepartment'))
    );
  }
  /** GET profTitles from the server */
  getProfTitles(): Observable<ProfTitle[]> {
    return this.http.get<ProfTitle[]>(this.profTitleUrl + ' ')
      .pipe(
        catchError(this.handleError<ProfTitle[]>('getProfTitles', []))
      );
  }
  /** GET profTitle by id. Will 404 if id not found */
  getProfTitle(id: number): Observable<ProfTitle> {
    const url = this.profTitleUrl + '?id=' + id;
    return this.http.get<ProfTitle>(url).pipe(
      catchError(this.handleError<ProfTitle>(`getProfTitle id=${id}`))
    );
  }

  /** POST: add a new profTitle to the server */
  addProfTitle(profTitle: { no: string; description: string; remarks: string }): Observable<Response> {
    return this.http.post<Response>(this.profTitleUrl + ' ', profTitle, httpOptions).pipe(
      catchError(this.handleError<any>('addProfTitle'))
    );
  }

  /** DELETE: delete the profTitle from the server */
  deleteProfTitle(profTitle: ProfTitle | number): Observable<Response> {
    const id = typeof profTitle === 'number' ? profTitle : profTitle.id;
    return this.http.delete<Response>(this.profTitleUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteProfTitle'))
    );
  }

  /** PUT: update the hero on the server */
  updateProfTitle(profTitle: ProfTitle): Observable<Response> {
    return this.http.put<Response>(this.profTitleUrl + ' ', profTitle, httpOptions).pipe(
      catchError(this.handleError<Response>('updateProfTitle'))
    );
  }
  /** GET teachers from the server */
  getTeachers(): Observable<Teacher[]> {
    return this.http.get<Teacher[]>(this.teacherUrl + ' ')
      .pipe(
        catchError(this.handleError<Teacher[]>('getTeachers', []))
      );
  }
  /** GET teacher by id. Will 404 if id not found */
  getTeacher(id: number): Observable<Teacher> {
    const url = this.teacherUrl + '?id=' + id;
    return this.http.get<Teacher>(url).pipe(
      catchError(this.handleError<Teacher>(`getTeacher id=${id}`))
    );
  }

  /** POST: add a new teacher to the server */
  // tslint:disable-next-line:max-line-length
  addTeacher(teacher: Teacher1): Observable<Response> {
    return this.http.post<Response>(this.teacherUrl + ' ', teacher, httpOptions).pipe(
      catchError(this.handleError<any>('addTeacher'))
    );
  }

  /** DELETE: delete the teacher from the server */
  deleteTeacher(teacher: Teacher | number): Observable<Response> {
    const id = typeof teacher === 'number' ? teacher : teacher.id;
    return this.http.delete<Response>(this.teacherUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteTeacher'))
    );
  }

  /** PUT: update the hero on the server */
  updateTeacher(teacher: Teacher): Observable<Response> {
    return this.http.put<Response>(this.teacherUrl + ' ', teacher, httpOptions).pipe(
      catchError(this.handleError<Response>('updateTeacher'))
    );
  }
  getDepSch(id: number): Observable<Department[]>{
    const url = this.departmentUrl + '?' + id;
    return this.http.get<Department[]>(url).pipe(
      catchError(this.handleError<Department[]>(`getDepSch schoolid=${id}`))
    );
  }
  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
