import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { Teacher } from '../entity';
import { EntityService } from '../entity.service';

@Component({
  selector: 'app-detaill',
  templateUrl: './detaillTeacher.component.html',
  styleUrls: ['./detaillTeacher.component.css']
})
export class DetaillTeacherComponent implements OnInit {

  @Input() teacher: Teacher;

  constructor(
    private route: ActivatedRoute,
    private teacherService: EntityService,
    private location: Location
  ) {}

  ngOnInit(): void {
    this.route.queryParams
           .subscribe((params: Teacher) => {
               this.teacher = params;
             });
  }

  goBack(): void {
    this.location.back();
  }
}
