import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { ProfTitle } from '../entity';
import { EntityService } from '../entity.service';

@Component({
  selector: 'app-detaill',
  templateUrl: './detaillProfTitle.component.html',
  styleUrls: ['./detaillProfTitle.component.css']
})
export class DetaillProfTitleComponent implements OnInit {

  @Input() profTitle: ProfTitle;

  constructor(
    private route: ActivatedRoute,
    private profTitleService: EntityService,
    private location: Location
  ) {}

  ngOnInit(): void {
    this.route.queryParams
           .subscribe((params: ProfTitle) => {
               this.profTitle = params;
             });
  }

  goBack(): void {
    this.location.back();
  }
}
