import { Component, OnInit } from '@angular/core';
import { ProfTitle } from '../entity';
import { EntityService } from '../entity.service';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-profTitle',
  templateUrl: './profTitle.component.html',
  styleUrls: ['./profTitle.component.css']
})
export class ProfTitleComponent implements OnInit {
  profTitles: ProfTitle[];
  constructor(private profTitleService: EntityService) { }

  ngOnInit() {
    this.getProfTitles();
  }
  getProfTitles(): void {
    this.profTitleService.getProfTitles ()
      .subscribe(res => this.profTitles = res);
  }
  delete(profTitle: ProfTitle | number): void {
    this.profTitles = this.profTitles.filter(h => h !== profTitle);
    this.profTitleService.deleteProfTitle(profTitle).subscribe();
  }
}
